body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color {
  background: #5f2c82;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #5f2c82, #49a09d); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* Landing Page */
.landing-grid {
  background: #5f2c82;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #5f2c82, #49a09d); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  background-size: cover;
  height: 95vh;
}

.avatar-img {
  height: 272px;
  padding: 2em;
}

.banner {
  background-color: black;
  opacity: 0.7;
  border-radius: 10px;
  width: 50%;
  margin: auto;
  color: white;
}

.banner h1 {
  font-size: 4em;
  padding-top: 0.5em;
}

.banner hr {
  border-top: 3px ridge white;
  width: 80%;
  margin: auto;
  border-radius: 3px;
}

.banner h2 {
  font-size: 2.2em;
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 25%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5.5em;
  padding: 5px;
  padding-bottom: 0.1em;
}

/* Projects Page */
.projects-grid {
  display: -webkit-flex;
  display: flex;
}

.ThermoSmart {
  top: 3em;
}

/* Resume Page */
iframe {
  height: 850px;
  width: 100%;
}

/*Contact Page */
.contact-body {
  padding-top: 2em;
  margin: auto;
  -webkit-justify-content: center;
          justify-content: center;
}

.contact-grid {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 78%;
  margin: auto;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
}

.avatar-img2 {
  height: 300px;
  padding-bottom: 2em;
}

.contact-grid hr {
  border-top: 4px gray dotted;
  width: 45%;
  margin: auto;
}

.contact-grid h2 {
  padding-top: 0.3em;
}

.contact-list i {
  padding-left: 2em;
  padding-right: 0.4em;
  font-size: 60px;
}

